@keyframes rotate-arrows {
  0% {
    transform: translate(-50%, -100%)rotate(0);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

.page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.watch {
  border: 2px dotted;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.watch__second {
  transform-origin: bottom;
  background-color: green;
  width: 3px;
  height: 50%;
  animation: 5s linear rotate-arrows;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.watch__minute {
  transform-origin: bottom;
  background-color: #00f;
  width: 4px;
  height: 30%;
  animation: 60s linear rotate-arrows;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.watch__centr {
  background-color: orange;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  display: flex;
  position: absolute;
}

